  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        focusList: [],
				isLogin: this.$store.state.isLogin
      }
    },
    onLoad() {
    	// var id = uni.getStorageSync('user').id
			// let id = JSON.parse(uni.getStorageSync('userInfo')).id
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
      uni.request({
        url: urls.HTTP + '/user/userConcern',
        method: 'POST',
        data: {
          uid: id
        },
        success: (res) => {
          // console.log(res)
          if (res.data.code === 0) {
            this.focusList = res.data.datalist
          } else {
            this.focusList = []
          }
        }
      })
    },
    methods: {
      logoUrl: function (pic) {
        return urls.UrlHttp + pic
      },
      seeTa: function (item) {
        console.log(item)
        this.$store.dispatch('otherUser', item)
        uni.navigateTo({
          url: '../common/book/otherUserInfo'
        })
      },
      focusTa: function (id) {
        let fuid = id
        let uid = this.userInfo.id
        if (!this.isFocus) {
          uni.request({
            url: urls.HTTP + '/user/removefollow',
            method: 'POST',
            data: {
              fuid: fuid,
              uid: uid
            },
            success: (res) => {
              // console.log(res)
              if (res.data.code === 0) {
                this.focusList = res.data.datalist
                uni.showToast({
                  title: '取消成功',
                  icon: 'none'
                })
              } else {
                this.focusList = []
                uni.showToast({
                  title: '取消成功',
                  icon: 'none'
                })
              }
            }
          })
        }
      }
    },
    computed: {
      userInfo () {
        return this.$store.state.userInfo
      }
    }
  }
